/* Header cu fundal uniform, fără bara de sub header */
.header {
  background: linear-gradient(45deg, #210535, #430d4b); /* Fundal uniform între violet închis și violet profund */
  color: white;
  padding: 15px 30px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-sizing: border-box;
  font-family: 'Arial', sans-serif;
  position: sticky; /* Sticky pentru header */
  top: 0; /* Face header-ul să rămână sus la scroll */
  z-index: 999; /* Asigurăm că header-ul rămâne deasupra altor elemente */
  margin: 0; /* Eliminăm orice margine */
  margin-bottom: 0; /* Eliminăm marginea inferioară */
  border-bottom: none; /* Eliminăm orice linie de jos */
}

/* Textul pentru titlu */
.header h1 {
  font-size: 3rem; /* Mărim fontul titlului */
  font-weight: 700;
  color: #f5d5e0; /* Text deschis pentru contrast */
  letter-spacing: 2px;
  text-shadow: 2px 2px 5px rgba(245, 213, 224, 0.7); /* Efect de strălucire */
  cursor: pointer;
}

/* Navbar pentru butoanele Login și Sign Up */
.nav {
  display: flex;
  align-items: center;
}

/* Navbar pentru butoanele Login și Sign Up */
.nav button {
  padding: 15px 40px; /* Mărim padding-ul pentru a face butoanele mai mari */
  margin-left: 20px;
  background: linear-gradient(45deg, #ff5f6d, #ffc3a0); /* Gradient frumos între două culori */
  color: white;
  border: 2px solid transparent; /* Border transparent pentru a scoate în evidență fundalul */
  font-size: 1.5rem; /* Mărim fontul pentru un text mai vizibil */
  font-weight: 600; /* Mărim greutatea fontului pentru un text mai îndrăzneț */
  border-radius: 30px; /* Colțuri rotunjite pentru un look mai modern */
  cursor: pointer;
  transition: all 0.3s ease-in-out; /* Efect de tranziție pentru hover */
  text-transform: uppercase; /* Textul va fi cu litere mari */
  box-shadow: 0 4px 8px rgba(255, 95, 109, 0.4); /* Adăugăm o umbră ușoară pentru efect de flotare */
}

/* Efectul la hover pentru butoane */
.nav button:hover {
  background: linear-gradient(45deg, #ff7f50, #ff6347); /* Schimbăm culorile gradientului pe hover */
  border-color: #ff5f6d; /* Folosim o culoare mai intensă pentru border */
  transform: scale(1.05); /* Mărire ușoară la hover */
  box-shadow: 0 6px 12px rgba(255, 95, 109, 0.5); /* Umbră mai puternică pentru efectul de flotare */
}

/* Efect la focus */
.nav button:focus {
  outline: none; /* Elimină outline-ul implicit al butoanelor */
  box-shadow: 0 0 15px 3px rgba(255, 95, 109, 0.8); /* Efect de iluminare puternic la focus */
  border-color: #ff5f6d; /* Adăugăm o margine mai puternică pe focus */
}

/* Media Queries pentru mobil */
@media (max-width: 768px) {
  .header {
    padding: 10px 15px; /* Micșorăm padding-ul pe mobil */
    flex-direction: column;
    align-items: center;
  }

  .header h1 {
    font-size: 2.5rem; /* Ajustăm dimensiunea fontului pe mobil */
  }

  .nav {
    margin-top: 10px;
  }

  .nav button {
    margin-left: 0;
    margin-top: 10px;
  }
}
