/* Resetare generală */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Arial', sans-serif;
  background-color: #1e1e3f;  /* Fundal închis, tematic cosmic */
  color: white;
}

.App {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

/* Body content adjustment to account for header space */
.body-content {
  margin-top: 100px;
}

/* Media Queries pentru mobile */
@media (max-width: 768px) {
  body {
    padding: 0 10px;
  }
}
