.main-banner {
  background: linear-gradient(45deg, #0d0d5e, #6a0dad, #ff00ff);  /* Fundal cu gradiente cosmice */
  background-size: cover;
  background-position: center;
  color: white;
  padding: 50px 20px; /* Reducem padding-ul pentru a micșora distanța dintre header și pagina principală */
  text-align: center;
  min-height: 55vh; /* Setăm o înălțime flexibilă, mai mică decât 100vh */
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
  margin-top: 0; /* Eliminăm orice margine de sus */
}

.main-banner h1 {
  font-size: 3rem;
  font-weight: bold;
  text-shadow: 3px 3px 10px rgba(255, 255, 255, 0.7), 0 0 25px rgba(0, 255, 204, 1);
  margin-bottom: 20px;
}

.main-banner p {
  font-size: 1.5rem;
  margin-bottom: 30px;
  font-family: 'Arial', sans-serif;
  text-shadow: 1px 1px 10px rgba(255, 255, 255, 0.7);
}

.main-banner button {
  padding: 15px 40px;
  font-size: 1.2rem;
  background-color: #00ffcc;
  color: white;
  border: 2px solid #00ffcc;
  cursor: pointer;
  border-radius: 30px;
  transition: all 0.3s ease;
  animation: pulse 1.5s infinite;
}

.main-banner button:hover {
  background-color: #00bfae;
  border-color: #00bfae;
  transform: translateY(-2px);
}

/* Pulse Animation */
@keyframes pulse {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.1);
  }
  100% {
    transform: scale(1);
  }
}

/* Media Queries pentru mobil */
@media (max-width: 768px) {
  .main-banner {
    padding: 100px 20px;
  }

  .main-banner h1 {
    font-size: 2rem;
  }

  .main-banner p {
    font-size: 1.2rem;
  }

  .main-banner button {
    padding: 12px 30px;
    font-size: 1rem;
  }
}
